.profile__avatar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.profile__avatar:hover {
  background-position: center;
  opacity: 0.2;
  cursor: pointer;
}

@media screen and (max-width: 400px) {
  .profile__avatar {
    margin-right: 0;
  }
}
