.card__image {
  height: 282px;
  min-height: 282px;
  width: 282px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.card__image:hover {
  cursor: pointer;
  opacity: 0.8;
}

@media screen and (max-width: 900px) {
  .card__image {
    width: 100%;
  }
}
